.container {
    display: flex;
    flex-direction: row;
    min-height: 100vh;
    position: relative;
    justify-content: center;
    align-items: center;
}

section {
    display: grid;
    place-items: center;
    align-content: center;
    min-height: 100vh;
    flex-grow: 1;
}

.hidden {
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-100%);
    transition: all 1s;
}

.visible {
    opacity: 1;
    filter: blur(0);
    transform: translateX(0px);
}

.rotate {
    animation: rotation 6s infinite linear;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}
